alculateRowHeight() }\">\n    <span class=\"step-icon\" @click=\"changeShowLog()\">\n      <i id=\"play-icon\" class=\"mdi mdi-play\" v-if=\"!showLog\"></i>\n      <i id=\"play-icon\" class=\"mdi mdi-menu-down\" v-else></i>\n    </span>\n    <span style=\"margin-right: 4px;\">\n      <i class=\"mdi mdi-timer-sand\" v-if=\"props.status == 'waiting'\"></i>\n      <i class=\"mdi mdi-arrow-up-circle-outline\" v-else-if=\"props.status == 'running'\"></i>\n      <i class=\"mdi mdi-check-circle-outline\" v-else-if=\"props.status == 'success'\"></i>\n      <i class=\"mdi mdi-close-circle-outline\" v-else-if=\"props.status == 'failed'\"></i>\n    </span>\n    <span>{{ props.name }}</span>\n    <div style=\"background-color: white; margin-top: 8px;\">\n      <div class=\"log-container\" v-if=\"showLog && !customSteps.includes(props.name)\">\n        <span class=\"log-text\" v-for=\"log in logs\">{{ log }}</span>\n      </div>\n    </div>\n    <div class=\"pipeline-started-container\" v-if=\"showLog && props.name == 'Pipeline Started'\">\n      <span class=\"pipeline-started-text\">\n        You can see the pipeline status by going to this link.\n        <a href=\"http://jenkins.rally.softtech/job/mr-lowcode/\"> GO TO LINK</a>\n      </span>\n    </div>\n    <div class=\"deployment-completed-container\" v-if=\"showLog && props.name == 'Deployment Completed'\">\n      <span><a>SERVICE URL</a></span>\n      <span><a>SERVICE UI</a></span>\n    </div>\n  </div>\n</template>\n<style scoped>\n.step-row {\n  display: inline-block;\n  width: 1302px;\n  height: 40px;\n  padding-top: 8px;\n  padding-bottom: 8px;\n  /* UI Properties */\n  background: #FFFFFF 0% 0% no-repeat padding-box;\n  opacity: 1;\n}\n\n.step-icon {\n  margin-left: 20px;\n  width: 32px;\n  float: left;\n}\n\n#play-icon {\n  color: #BCBCBC;\n  padding-top: 12px;\n  padding-bottom: 12px;\n  cursor: pointer;\n}\n\n.log-container {\n  display: block;\n  margin-left: 52px;\n  height: 88px;\n  width: 883px;\n  overflow: auto;\n}\n\n.log-container::-webkit-scrollbar {\n  display: none;\n}\n\n.log-text {\n  display: block;\n  width: 883px;\n  font-size: smaller;\n  font-weight: 600;\n  font-family: monospace;\n}\n\n.pipeline-started-container {\n  align-content: center;\n  display: grid;\n  height: 69px;\n  margin-top: 8px;\n  padding-left: 20px;\n  background: white;\n}\n\n.deployment-completed-container {\n  height: 69px;\n  margin-top: 8px;\n  padding-left: 20px;\n  background: white;\n}\n\n.pipeline-started-text {\n  display: block;\n  width: 883px;\n  font: normal normal normal 14/20px Roboto;\n  letter-spacing: 0px;\n  color: #616161;\n}\n\n.mdi-timer-sand {\n  color: #A1A1A1;\n}\n\n.mdi-arrow-up-circle-outline {\n  color: #449DD1;\n}\n\n.mdi-check-circle-outline {\n  color: #149911;\n}\n\n.mdi-close-circle-outline {\n  color: #DF2935;\n}\n</style>\n"],"sourceRoot":""}]);
